<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="工号">
          <el-input v-model="listQuery.user_id"></el-input>
        </el-form-item>
        <el-form-item label="用户名">
          <el-input v-model="listQuery.user_name"></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-select v-model="listQuery.status" placeholder="请选择">
            <el-option label="全部" value=""></el-option>
            <el-option label="启用" value="启用"></el-option>
            <el-option label="禁用" value="禁用"></el-option>
          </el-select>
        </el-form-item>
        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>人员列表</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addBtn"
              ><i class="el-icon-plus"></i> 新增</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>
    <!-- 新增人员弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="600px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-form-item label="工号" prop="user_id">
          <el-input v-model="dialogForm.user_id"></el-input>
        </el-form-item>
        <el-form-item label="用户名" prop="user_name">
          <el-input v-model="dialogForm.user_name"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="user_password">
          <el-input
            v-model="dialogForm.user_password"
            type="password"
          ></el-input>
        </el-form-item>

        <el-form-item label="权限" prop="user_authorityArr">
          <el-select
            v-model="dialogForm.user_authorityArr"
            placeholder="请选择"
            multiple
            style="width: 100%"
          >
            <el-option
              v-for="(item, index) in WorkCodeNameList"
              :key="index + ' search'"
              :label="item.work_name"
              :value="item.work_id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="状态" prop="status">
          <el-select
            v-model="dialogForm.status"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option label="启用" value="启用"></el-option>
            <el-option label="禁用" value="禁用"></el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "UserList",
  components: {
    ELTable,
  },
  data() {
    return {
      // 筛选
      listQuery: {
        user_id: "",
        user_name: "",
        status: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        user_id: "",
        user_name: "",
        status: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 新增人员弹出层
      dialogTitle: "",
      dialogFlag: false,
      dialogForm: {
        user_code: "",
        user_id: "",
        user_name: "",
        user_password: "",
        user_authorityArr: [],
        user_authority: "",
        status: "启用",
      },
      dialogFormBase: {
        user_code: "",
        user_id: "",
        user_name: "",
        user_password: "",
        user_authorityArr: [],
        user_authority: "",
        status: "启用",
      },
      dialogFormRules: {
        user_id: [{ required: true, message: "请输入员工号", trigger: "blur" }],
        user_name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        user_password: [
          { required: true, message: "请输入密码", trigger: "blur" },
        ],
        status: [{ required: true, message: "请选择状态", trigger: "change" }],
      },
      // 表格列
      tableCol: [
        {
          label: "员工内码",
          field: "user_code",
          align: "center",
        },
        {
          label: "工号",
          field: "user_id",
          align: "center",
        },
        {
          label: "用户名",
          field: "user_name",
          align: "center",
        },
        {
          label: "权限",
          field: "authorityStrArr",
          align: "center",
        },
        {
          label: "状态",
          field: "status",
          align: "center",
        },
        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },

  computed: {
    ...mapState(["WorkCodeNameList"]),
  },
  methods: {
    ...mapActions(["getLogisticWorkCodeName"]),
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticPersonDeal(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticPersonDeal(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticPersonDeal(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticPersonDeal(this.listQuery);
    },
    // 人员列表 查询
    async getLogisticPersonDeal(params) {
      // 工站内码和工站名称接口： 查询
      await this.getLogisticWorkCodeName();

      baseManagement.getLogisticPersonDeal(params).then((res) => {
        console.log("人员列表 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;
          tableData.forEach((tableDataItem) => {
            let authorityArr = tableDataItem.user_authority.split(",");
            let authorityStrArr = "";
            authorityArr.forEach((authorityArrItem) => {
              this.WorkCodeNameList.forEach((CodeNameListItem) => {
                if (authorityArrItem == CodeNameListItem.work_id) {
                  authorityStrArr += CodeNameListItem.work_name + ",";
                }
              });
            });
            authorityStrArr = authorityStrArr.substring(
              0,
              authorityStrArr.length - 1
            );
            tableDataItem.authorityStrArr = authorityStrArr;
          });
          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 人员列表 新增
    postLogisticPersonDeal(params) {
      baseManagement.postLogisticPersonDeal(params).then((res) => {
        console.log("人员列表 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticPersonDeal(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 人员列表 新增
    addBtn() {
      this.dialogTitle = "新增人员";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
    },

    // 人员列表 修改
    colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改人员";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      let authorityArr = [];
      if (rowData.user_authority) {
        authorityArr = rowData.user_authority.split(",");
      }

      this.dialogForm = {
        user_code: rowData.user_code,
        user_id: rowData.user_id,
        user_name: rowData.user_name,
        user_password: rowData.user_password,
        user_authority: rowData.user_authority,
        user_authorityArr: authorityArr,
        status: rowData.status,
      };
    },

    // 人员列表 修改
    getLogisticPutPerson(params) {
      baseManagement.getLogisticPutPerson(params).then((res) => {
        console.log("人员列表 修改", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticPersonDeal(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 弹出层确认按钮
    dialogSave() {
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口

          if (
            this.dialogForm.user_authorityArr &&
            this.dialogForm.user_authorityArr.length > 0
          ) {
            let authorityStr = "";
            this.dialogForm.user_authorityArr.forEach((item) => {
              authorityStr += item + ",";
            });
            authorityStr = authorityStr.substring(0, authorityStr.length - 1);
            this.dialogForm.user_authority = authorityStr;
          }
          if (this.dialogTitle == "新增人员") {
            console.log("新增人员", this.dialogForm);
            this.postLogisticPersonDeal(this.dialogForm);
          } else if (this.dialogTitle == "修改人员") {
            console.log("修改人员", this.dialogForm);
            this.getLogisticPutPerson(this.dialogForm);
          }
        }
      });
    },

    // 人员列表 删除
    getLogisticDeletePerson(params) {
      baseManagement.getLogisticDeletePerson(params).then((res) => {
        console.log("人员列表 删除", res);
        if (res.code == 0) {
          this.getLogisticPersonDeal(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.getLogisticDeletePerson({ user_code: rowData.user_code });
      });
    },
  },
  mounted() {
    // 人员列表 查询
    this.getLogisticPersonDeal(this.listQuery);
  },
};
</script>

